#container{
    width: 100%;
    height: 70%;
}

#kata-kata{
    width: 50%;
    height: 100%;
    padding-top: 80px;
    padding-left: 50px;
}

#gambar-banner{
    width: 50%;
    height: 100%;
}

#kata-promote{
    width: 370px;
    height: 300px;
    padding: 0px 0px 30px 0px;
    font-size: 20px;

}

