#Information{
    width: 100%;
    height: 20vh;
    /* display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center; */
}

#box-button{
    width: auto;
    height: auto;
    padding: 30px;
    background-color: white;
    border-radius: 20px;
    box-shadow: 0px 8px 30px grey;
}

#box-button button{
    margin-bottom: 20px;
}

#sosial-media{
    width: auto;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

#button-sosial-media{
    margin-right: 50px;
    width: 100px;
}
#button-nav-sosmed{
    margin-bottom: 80px;
}

@media only screen and (max-width: 1026px) { 
    #gambar-banner { 
      display: none; 
    } 

    #box-button{
        justify-content: center;
        margin-bottom: 40px;
    }

    #button-nav-sosmed{
        height: 400px;
        padding: 50px;
    }
  }
  
